export default {
	props: {
		queryKey: {
			type: String,
			default: ''
		}
	},

	data() {
		return {
			inputChange: false,
			clearIconShow: false,
			placeholder: '搜索图书名称',
			query: ''
		}
	},

	methods: {
		onBindFocus(e) { // 获取焦点事件处理
			this.inputChange = true
			this.placeholder = '搜索图书名称'

			this.$emit('on-focus', {
				searchResultShow: true
			})
		},

		onConfirmSearch(e) { // 手机键盘点击搜索触发
			if (this.query) {
				this.$emit('on-confirm-search', {
					query: this.query
				})
			} else {
				this.placeholder = '请输入搜索内容'
			}
		},

		onSearchIconTap(e) { // 搜索图标点击搜索处理
			if (this.query) {
				this.$emit('on-confirm-search', {
					query: this.query
				})
			} else {
				this.placeholder = '请输入搜索内容'
			}
		},

		onClearTap() { // 清空搜索框事件	
			this.query = ''

			this.$emit('on-cancel', {
				searchResultShow: true
			})
		},

		onCancelTap() { // 取消按钮事件
			this.inputChange = false
			this.query = ''

			this.$emit('on-cancel', {
				searchResultShow: false
			})
		}
	},

	watch: {
		query(query) {
			if (query) {
				this.clearIconShow = true
			} else {
				this.clearIconShow = false
			}
		},
		
		queryKey(query) {
			if (query) {
				this.query = query
			}
		}
	}
}
